@font-face {
  font-family: "Poppins";
  font-weight: 500;
  font-style: normal;
  font-display: swap;
  src: local(Poppins-Medium), url("../fonts/poppins-medium.woff2") format("woff2"), url("../fonts/poppins-medium.woff") format("woff"); }

@font-face {
  font-family: "Poppins";
  font-weight: 700;
  font-style: normal;
  font-display: swap;
  src: local(Poppins-Bold), url("../fonts/poppins-bold.woff2") format("woff2"), url("../fonts/poppins-bold.woff") format("woff"); }

@font-face {
  font-family: "Poppins";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: local(Poppins-Regular), url("../fonts/poppins-regular.woff2") format("woff2"), url("../fonts/poppins-regular.woff") format("woff"); }

@font-face {
  font-family: "CormorantGaramond";
  font-weight: 700;
  font-style: normal;
  font-display: swap;
  src: local(CormorantGaramond-Bold), url("../fonts/cormorant-garamond-bold.woff2") format("woff2"), url("../fonts/cormorant-garamond-bold.woff") format("woff"); }

@font-face {
  font-family: "CormorantGaramond";
  font-weight: 600;
  font-style: normal;
  font-display: swap;
  src: local(CormorantGaramond-SemiBold), url("../fonts/cormorant-garamond-semi-bold.woff2") format("woff2"), url("../fonts/cormorant-garamond-semi-bold.woff") format("woff"); }

@font-face {
  font-family: "CormorantGaramond";
  font-weight: 400;
  font-style: italic;
  font-display: swap;
  src: local(CormorantGaramond-Italic), url("../fonts/cormorant-garamond-italic.woff2") format("woff2"), url("../fonts/cormorant-garamond-italic.woff") format("woff"); }

@font-face {
  font-family: "CormorantGaramond";
  font-weight: 500;
  font-style: italic;
  font-display: swap;
  src: local(CormorantGaramond-MediumItalic), url("../fonts/cormorant-garamond-medium-italic.woff2") format("woff2"), url("../fonts/cormorant-garamond-medium-italic.woff") format("woff"); }

html {
  scroll-behavior: smooth; }

.body {
  margin: 0;
  position: relative;
  min-height: 100vh;
  display: grid;
  grid-template-rows: auto 1fr auto;
  font-family: "Poppins", Arial, sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  color: #1d1613;
  background-color: white;
  -webkit-box-sizing: border-box;
          box-sizing: border-box; }

.container {
  margin: 0 auto;
  max-width: 1440px; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(100%);
          clip-path: inset(100%);
  overflow: hidden; }

.link {
  font-family: "Poppins", Arial, sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  color: #1d1613;
  text-decoration: none; }

.list {
  list-style: none;
  margin: 0;
  padding: 0; }

*,
*::before,
*::after {
  -webkit-box-sizing: inherit;
          box-sizing: inherit; }
